@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');

h1 {
    font-size: 48px;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 24px;
}

h4 {
    font-size: 18px;
}

h5 {
    font-size: 14px;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
    margin: 0;
}

p {
    font-size: 18px;
    font-weight: 400;
    margin: 0.5em 0;
}

@media screen and (max-width: $break-phone) {
    h1 {
        font-size: 24px;
    }
    
    h2 {
        font-size: 18px;
    }
    
    h3, h4 {
        font-size: 13px;
    }
    
    p {
        font-size: 12px;
    }
    
    h5 {
        font-size: 10px;
    }
}